// Custom increase bootstrap container
@media (min-width: 1440px) {
  .container {
    max-width: 1380px;
  }
}

@media (max-width: 1605px) {
  .page-section {
    // section type
    &__hero {
      height: 700px;
    }

    &__cases {
      // height: 1500px;
    }

    &__news {
      height: 1100px;
    }

    &__team {
      height: 900px;
    }
  }

  .team-card {
    &--1 {
      position: absolute;
      left: 5%;

      .team-card__content {
        margin-top: -70px;
      }
    }

    &--2 {
      position: absolute;
      top: 300px;
      left: 32%;

      .team-card__content {
        margin-top: -60px;
        margin-right: -40px;
        margin-left: 40px;
      }
    }

    &--3 {
      position: absolute;
      right: 7%;
      top: 171px;

      .team-card__content {
        margin-left: -56px;
        margin-right: 56px;
      }
    }
  }
}

@media (max-width: 1200px) {
  .page-section {
    // section type
    &__hero {
      height: 700px;

      .page-section__pipe {
        width: 528.8px;
      }
    }

    // &__cases {
    //   // height: 1500px;
    // }

    &__news {
      height: 900px;

      .page-section__pipe {
        top: -160px;
      }
    }

    &__team {
      height: 900px;
    }
  }

  .case-card {
    &__pipe-1 {
      top: 300px;
    }
    &__pipe-2 {
      top: 315px;
    }
    &__pipe-3 {
      top: 580px;
    }

    &--2 {
      &::after {
        top: 460px;
        width: 70px;
        height: 150px;
      }
    }
  }

  .news-card {
    &--1 {
      z-index: 5;
      width: 30.7%;

      top: 29%;
      right: 2.2%;
    }
    &--2 {
      z-index: 3;
      width: 46.6%;

      top: 40.7%;
      left: 5.6%;
    }
    &--3 {
      z-index: 4;
      width: 38.65%;

      top: 59.1%;
      right: 23.2%;
    }
  }

  .team-card {
    &--3 {
      right: 0;
    }
  }
}
@media (max-width: 992px) {
  .contact-form {
    flex-wrap: wrap;

    &__splash {
      background-position: bottom;
      background-image: url("../assets/contact-form-mobile.png");

      &-title {
        font-size: 3rem;
      }
    }

    &__content {
      padding: 30px 20px;
      &-title {
        font-size: 1rem;
      }

      .dd-button {
        width: 100%;
      }
    }
  }
}
@media (max-width: 768px) {
  .main-logo {
    width: 150px;
  }

  .dd-button {
    font-size: 0.875rem;
    padding-left: 20px;
    padding-right: 20px;
  }

  .page-section {
    padding: 10px 0;
    &__title {
      font-size: 1.5rem;
    }
    // section type
    &__hero {
      &-title {
        font-size: 1.5rem;
      }
      &-subtitle {
        font-size: 1.125rem;
      }
      &-partners {
        display: flex;
        justify-content: center;
        img {
          width: 25%;
        }
      }

      .page-section__pipe {
        left: -40px;
        width: 450px;
        max-width: none;
      }
    }

    &__parallax {
      padding: 100px 0;
      &-title {
        font-size: 1.5rem;
        margin-bottom: 1.875rem;
      }
      &-content {
        font-size: 0.875rem;
      }

      .container {
        padding: 0;
      }
    }

    &__news {
      padding-top: 100px;
      height: auto;
      .page-section__pipe {
        top: -40px;
        width: 900px;
        max-width: none;
        height: 1800px;
        left: -50px;
      }
    }

    &__team {
      height: 1400px;
      padding-top: 80px;
      &-title {
        font-size: 1.5rem;
      }
      &-text {
        font-size: 0.875rem;
      }

      .page-section__pipe {
        top: 325px;
        left: -150px;
        width: 100%;
        height: 1200px;
        min-width: 1000px;
        z-index: 2;
        transform: rotateZ(11deg) rotateX(-12deg);
      }
    }
  }

  .case-card {
    &-wrap {
      &--1 {
        z-index: 5;
      }
    }

    min-height: 650px;
    padding: 30px 20px;
    margin-bottom: 20px;

    &__tags {
      display: flex;
      flex-wrap: wrap;
    }

    &__tag {
      font-size: 0.625rem;
    }

    &__title {
      font-size: 3rem;
    }

    &__description {
      font-size: 1.125rem;
    }

    &__step {
      &-title {
        font-size: 1.125rem;
      }
      &-content {
        font-size: 0.875rem;
      }
    }

    &__content {
      .row {
        .col-md-6 {
          &:nth-child(3) {
            .case-card__step-content {
              width: 60%;
            }
          }
          &:nth-child(4) {
            .case-card__step-content {
              width: 60%;
            }
          }
        }
      }
    }

    &__pipe-1 {
      display: none;
    }
    &__pipe-2 {
      left: -94px;
      top: -75px;
      width: 1400px;
      max-width: none;
    }
    &__pipe-3 {
      max-width: none;
      right: -770px;
      top: 550px;
      transform: rotateX(4deg);
    }

    &--1 {
      position: relative;
      .case-card__image {
        width: 270px;
        right: -130px;
        bottom: 35px;
      }
    }

    &--2 {
      .case-card__image {
        bottom: -50px;
        right: -110px;
      }

      &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        background: linear-gradient(90deg, #fdc227 0, #fda327 100%);
        width: 145px;
        height: 300px;
        border-top-left-radius: 20px;
        border-top-right-radius: 50%;
        z-index: 3;
      }
    }

    &--3 {
      .case-card__image {
        width: 178px;
        bottom: -30px;
        right: -15px;
      }

      &::after {
        content: "";
        position: absolute;
        bottom: 0;
        right: 0;
        background: #370084;
        width: 65px;
        height: 80px;
        border-bottom-right-radius: 20px;
        z-index: 3;
      }
    }

    .dd-button {
      width: 100%;
      text-align: center;
    }
  }

  .parallax-element {
    &--1 {
      top: 50px;
      left: -40px;

      transform: translateY(100%);
    }
    &--2 {
      top: 55px;
      left: 0;

      transform: translateY(100%);
    }
    &--3 {
      top: 120px;
      right: -40px;

      transform: translateY(100%);
    }
    &--4 {
      top: 455px;
      right: 0;

      transform: translateY(100%);
    }
    &--5 {
      top: 515px;
      right: 55px;
      width: 60px;

      transform: translateY(100%);
    }
    &--6 {
      width: 60px;
      bottom: -100px;
      left: 0;
      z-index: 2;

      transform: translateY(100%);
    }
    &--7 {
      bottom: -105px;
      left: -45px;
      z-index: 3;

      transform: translateY(100%);
    }
  }

  .news-card {
    position: relative;
    padding: 20px 20px;
    width: 100% !important;
    max-width: none !important;
    left: 0 !important;
    right: 0 !important;
    top: 0 !important;
    margin-bottom: 20px;

    &--vertical {
      max-height: 600px;
      max-width: 435px;
      height: 100%;
      min-height: 490px;
    }
    &--horizontal {
      max-height: 450px;
      max-width: 660px;
      height: 100%;
      min-height: 330px;

      .news-card__text {
        width: 56%;
      }
    }
    &--video {
      max-width: 546px;
      min-width: 380px;
      // position: relative;
      padding: 0;

      &::after {
        content: "";
        position: absolute;
        background-image: url(../assets/play.png);
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: 500;
        width: 56px;
        height: 56px;
        pointer-events: none;
      }

      img {
        border-radius: 10px;
      }
    }

    &__title {
      font-size: 1.125rem;
    }
    &__text {
      font-size: 0.875rem;
    }
  }

  .team-card {
    width: 100%;
    padding: 10px;

    &__image {
      width: 260px;
    }
    &__content {
      &-name {
        font-size: 1.125rem;
      }
      &-position,
      &-team {
        font-size: 0.875rem;
        display: inline-block;
      }
    }

    &--1 {
      top: 300px;
      left: 0px;
      .team-card__image {
        margin-left: 40px;
      }
    }

    &--2 {
      top: 680px;
      left: 32%;
      transform: translateX(-32%);

      .team-card__image {
        margin-left: 40px;
      }
      .team-card__content {
        margin-left: 0;
        margin-right: 0;
      }
    }

    &--3 {
      top: 1050px;
      right: 0;
      .team-card__image {
        margin-left: 40px;
      }
      .team-card__content {
        margin-left: 0;
        margin-right: 0;
      }
    }
  }

  .parallax-element {
    max-width: 100px;
  }
}
@media (max-width: 576px) {
  .dd-button {
    font-size: 0.875rem;
    padding-left: 20px;
    padding-right: 20px;
  }
  .main-logo {
    width: 110px;
  }
}
@media (max-width: 506) {
  .case-card {
    &__pipe-3 {
      transform: rotateX(5deg);
    }
  }
}
@media (max-width: 390px) {
  .case-card {
    &__pipe-3 {
      transform: rotateX(6deg);
    }
  }
}
